import { useAuth } from "@/hooks/use-auth";
import { Layout } from "@/components/Layout";
import { EquityChart } from "@/components/EquityChart";
import { LoadingScreen } from "@/components/LoadingScreen";
import { useAccount, useAccountHistory, useUpdateAccount, useDeleteAccount, useAccountProfit } from "@/hooks/use-accounts";
import { useRoute, Redirect, useLocation } from "wouter";
import { motion } from "framer-motion";
import { ArrowLeft, Copy, Trash2, Edit2, Check, AlertCircle, TrendingUp, TrendingDown } from "lucide-react";
import { useState } from "react";
import { clsx } from "clsx";
import { Link } from "wouter";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogTrigger } from "@/components/ui/dialog";
import { useToast } from "@/hooks/use-toast";
import { formatCurrency, formatPercent } from "@/lib/format";

export default function AccountDetail() {
  const { user, isLoading: authLoading } = useAuth();
  const [, params] = useRoute("/accounts/:id");
  // Parse id defensively  if it's invalid, render loading until we have a valid numeric id
  const parsedId = params && params.id ? Number.parseInt(params.id as string, 10) : NaN;
  const id = Number.isFinite(parsedId) && !Number.isNaN(parsedId) ? parsedId : undefined;
  const [, setLocation] = useLocation();
  const { toast } = useToast();

  const { data: account, isLoading: accountLoading } = useAccount(id as any);
  const [period, setPeriod] = useState<"1D" | "1W" | "1M" | "1Y" | "ALL">("1D");
  const { data: history, isLoading: historyLoading } = useAccountHistory(id as any, period);
  
  const updateAccount = useUpdateAccount();
  const deleteAccount = useDeleteAccount();

  // Hooks must be called unconditionally and before any early return.
  // Ensure `useAccountProfit` runs every render to stabilize hook order.
  const tradeProfitQuery = useAccountProfit(id as any, period);
  const derivedProfit = tradeProfitQuery.data ?? 0;

  const [isEditing, setIsEditing] = useState(false);
  const [newName, setNewName] = useState("");
  const [copied, setCopied] = useState(false);
  const [showDelete, setShowDelete] = useState(false);

  if (authLoading) return <LoadingScreen />;
  if (!user) return <Redirect to="/login" />;
  if (!accountLoading && !account) return <Redirect to="/accounts" />;

  const apiUrl = `${window.location.origin}/api/webhook/mt5/${account?.token}`;

  const handleCopy = async () => {
    try {
      if (navigator.clipboard && navigator.clipboard.writeText) {
        await navigator.clipboard.writeText(apiUrl);
      } else {
        // Fallback for non-secure contexts
        const ta = document.createElement('textarea');
        ta.value = apiUrl;
        document.body.appendChild(ta);
        ta.select();
        document.execCommand('copy');
        document.body.removeChild(ta);
      }
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
      toast({ title: "Copied!", description: "API URL copied to clipboard" });
    } catch (err) {
      toast({ title: "Copy failed", description: "Unable to copy URL. You can manually select and copy it." });
    }
  };

  const handleUpdate = async () => {
    if (!newName.trim()) return;
    if (id === undefined) return;
    await updateAccount.mutateAsync({ id: id as number, name: newName });
    setIsEditing(false);
    toast({ title: "Updated", description: "Account name updated successfully" });
  };

  const handleDelete = async () => {
    try {
      if (id === undefined) return;
      await deleteAccount.mutateAsync(id as number);
      toast({ title: "Deleted", description: "Account removed successfully" });
      setLocation("/accounts");
    } catch (err: any) {
      toast({ title: "Delete failed", description: err.message || "Failed to delete account" });
    }
  };

  // Derive profit from trades-only endpoint
  const hist = history || [];
  const initialDeposit = hist.length > 0 ? hist[0].balance : (account?.balance ?? 0);
  const currentBalance = hist.length > 0 ? hist[hist.length - 1].balance : (account?.balance ?? 0);
  

  // If the id param is missing or invalid, show loading instead of crashing during hydration
  if (id === undefined) return <LoadingScreen />;

  // Unified percentage calculation for all periods:
  // Return % = Profit / (Balance - Profit) * 100
  let derivedProfitPercent = 0;
  const denom = (currentBalance - derivedProfit);
  derivedProfitPercent = denom > 0 ? (derivedProfit / denom) * 100 : 0;

  const isProfit = derivedProfit >= 0;

  return (
    <Layout>
      <div className="space-y-8">
        {/* Navigation & Header */}
        <div className="flex flex-col gap-4">
          <div className="flex items-center gap-3">
            <Link href="/accounts" className="p-2 bg-white/5 rounded-full hover:bg-white/10 transition-colors shrink-0">
              <ArrowLeft className="w-5 h-5 text-zinc-400" />
            </Link>
            <div className="flex-1 min-w-0">
              {isEditing ? (
                <div className="flex items-center gap-2 flex-wrap w-full">
                  <input 
                    value={newName} 
                    onChange={(e) => setNewName(e.target.value)}
                    placeholder={account?.name}
                    className="bg-white/10 border border-white/20 rounded px-2 py-1 text-2xl md:text-3xl font-bold text-white focus:outline-none flex-1 min-w-0"
                    autoFocus
                  />
                  <div className="flex items-center gap-2">
                    <button onClick={handleUpdate} className="p-1.5 bg-emerald-500/20 text-emerald-400 rounded hover:bg-emerald-500/30">
                      <Check className="w-4 h-4" />
                    </button>
                    <button onClick={() => setIsEditing(false)} className="text-zinc-500 text-sm hover:text-white px-2">Cancel</button>
                  </div>
                </div>
              ) : (
                <h1 className="text-2xl md:text-3xl font-bold text-white tracking-tight break-words">{account?.name}</h1>
              )}
            </div>
            {!isEditing && (
              <button 
                onClick={() => { setIsEditing(true); setNewName(account?.name || ""); }}
                className="p-2 bg-white/5 rounded-full hover:bg-white/10 transition-colors shrink-0"
              >
                <Edit2 className="w-4 h-4 text-zinc-400" />
              </button>
            )}
          </div>
        </div>

        {/* Stats Summary */}
        <div className="flex flex-col md:flex-row justify-between items-start md:items-end gap-6">
          <div>
            <h2 className="text-zinc-500 text-sm font-medium mb-1">Balance</h2>
            <h1 className="text-2xl md:text-3xl font-bold text-white tracking-tight">
              {accountLoading ? "..." : formatCurrency(account?.balance || 0)}
            </h1>
          </div>
          <div className="w-full md:w-auto">
            <p className="text-zinc-400 font-medium mb-1">Profit ({period === "ALL" ? "All" : period.replace("1", "")})</p>
            <div className={clsx(
              "text-3xl md:text-4xl font-bold flex items-center gap-2",
              isProfit ? "text-emerald-400" : "text-rose-400"
            )}>
              {isProfit ? <TrendingUp className="w-8 h-8 md:w-10 md:h-10" /> : <TrendingDown className="w-8 h-8 md:w-10 md:h-10" />}
              <span>{formatCurrency(derivedProfit)}</span>
              <span className="text-lg bg-white/[0.03] px-3 py-1 rounded-lg ml-1">{formatPercent(derivedProfitPercent)}</span>
            </div>
          </div>
        </div>

        {/* Chart */}
        <EquityChart 
          data={history ? history.map(h => ({ ...h, timestamp: typeof h.timestamp === 'string' ? h.timestamp : new Date(h.timestamp).toISOString() })) : undefined}
          onPeriodChange={setPeriod} 
          isLoading={historyLoading} 
        />

        {/* API Connection Section */}
        <motion.div 
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2 }}
          className="glass-panel p-4 md:p-8 rounded-2xl md:rounded-3xl border border-white/5 overflow-hidden"
        >
          <div className="flex flex-col gap-4">
            <div className="flex items-start gap-3">
              <div className="p-2.5 md:p-3 bg-blue-500/10 rounded-xl text-blue-400 shrink-0">
                <AlertCircle className="w-5 h-5 md:w-6 md:h-6" />
              </div>
              <div>
                <h3 className="text-base md:text-lg font-bold text-white mb-1">Connect MetaTrader 5</h3>
                <p className="text-zinc-400 text-xs md:text-sm">
                  Add this URL to your MT5 Expert Advisor settings to enable real-time tracking.
                </p>
              </div>
            </div>

            <div className="flex flex-col gap-2 bg-black/40 border border-white/10 rounded-xl p-2 md:p-3">
              <code className="font-mono text-[10px] md:text-sm text-zinc-300 break-all py-1 px-2 select-all leading-relaxed">
                {apiUrl}
              </code>
              <button
                onClick={handleCopy}
                className={clsx(
                  "w-full px-4 py-2.5 rounded-lg transition-all font-medium flex items-center justify-center gap-2",
                  copied ? "bg-emerald-500/20 text-emerald-400" : "bg-white/10 text-white hover:bg-white/20"
                )}
              >
                {copied ? <Check className="w-4 h-4" /> : <Copy className="w-4 h-4" />}
                <span className="text-sm">{copied ? "Copied" : "Copy URL"}</span>
              </button>
            </div>
          </div>
        </motion.div>

        {/* Delete Account Section */}
        <div className="pt-8 border-t border-white/5">
          <Dialog open={showDelete} onOpenChange={setShowDelete}>
            <DialogTrigger asChild>
              <button 
                className="flex items-center gap-2 px-4 py-2 bg-red-500/10 text-red-400 rounded-lg hover:bg-red-500/20 transition-colors text-sm font-medium"
                data-testid="button-delete-account"
              >
                <Trash2 className="w-4 h-4" />
                <span>Delete Account</span>
              </button>
            </DialogTrigger>
            <DialogContent className="glass-panel border-white/10 bg-zinc-950/90 text-white sm:max-w-[425px] p-0 overflow-hidden">
              <div className="p-6">
                <DialogHeader className="mb-4">
                  <DialogTitle className="text-xl font-bold tracking-tight">Delete Account?</DialogTitle>
                  <DialogDescription className="text-zinc-400">
                    Are you sure you want to delete this account? This action cannot be undone and all historical data will be lost.
                  </DialogDescription>
                </DialogHeader>
                <div className="flex justify-center gap-3 pt-4">
                  <button onClick={() => setShowDelete(false)} className="px-4 py-2 rounded-lg text-sm font-medium text-zinc-400 hover:text-white hover:bg-white/5 transition-colors">Cancel</button>
                  <button 
                    onClick={handleDelete} 
                    className="px-6 py-2 rounded-lg bg-red-600 text-white hover:bg-red-700 font-semibold text-sm shadow-lg shadow-red-900/20 transition-colors"
                    data-testid="button-confirm-delete"
                  >
                    Delete Permanently
                  </button>
                </div>
              </div>
            </DialogContent>
          </Dialog>
        </div>
      </div>
    </Layout>
  );
}
